        <Footer></Footer>
    </div>
</template>
<script>
    import Header from "../components/Header";
    import Footer from "../components/Footer"

    export default {
        components: {
            Header,
            Footer
        },
        data() {
            return {
                university: [],
                is_active: 0,
                page: 1,
                total: 2,
                perPage: 1,
                university_one:[],
                show_cards:false
            }
        },
        mounted() {
            this.$axios.get('/university/?is_world=1&&page=1',{
                headers:{
                    'Accept-Language':localStorage.getItem('lang')
                }
            })
                .then(res => {
                    this.university = res.data
                })
        },
        methods: {
            get_univer_item(item){
                this.$axios.get(`/university/${item.id}`,{
                    headers:{
                        'Accept-Language':localStorage.getItem('lang')
                    }
                })
                    .then(res => {
                        this.university_one = res.data
                        this.show_cards=true
                    })
            },
            getIsCountry() {
                this.$axios.get('/university/?is_country=1&&page=1',{
                    headers:{
                        'Accept-Language':localStorage.getItem('lang')
                    }
                })
                    .then(res => {
                        this.university = res.data
                    })
            },
            getIsworld() {
                this.$axios.get('/university/?is_world=1&&page=1',{
                    headers:{
                        'Accept-Language':localStorage.getItem('lang')
                    }
                })
                    .then(res => {
                        this.university = res.data
                    })
            },
            getProfi() {
                this.$axios.get('/university/?is_prof_education=1&&page=1',{
                    headers:{
                        'Accept-Language':localStorage.getItem('lang')
                    }
                })
                    .then(res => {
                        this.university = res.data
                    })
            }
        },
        watch: {
            page(page) {
                if (this.is_active == 0) {
                    this.$axios.get(`/university/?is_world=1&&page=${page}`,{
                        headers:{
                            'Accept-Language':localStorage.getItem('lang')
                        }
                    })
                        .then(res => {
                            this.university = res.data
                        })
                }
                if (this.is_active == 1) {
                    this.$axios.get(`/university/?is_country=1&&page=${page}`,{
                        headers:{
                            'Accept-Language':localStorage.getItem('lang')
                        }
                    } )
                        .then(res => {
                            this.university = res.data
                        })
                }
                if (this.is_active == 2) {
                    this.$axios.get(`/university/?is_prof_education=1&&page=${page}`,{
                        headers:{
                            'Accept-Language':localStorage.getItem('lang')
                        }
                    } )
                        .then(res => {
                            this.university = res.data
                        })
                }

            },
        },
    }
</script>