                  @mousedown="startDrag"
              />
              <div class="overlay">
                <div class="crop-circle"></div>
              </div>
            </div>
            <input type="file" @change="uploadImage" accept="image/*" />
            <div class="controls">
              <div class="slider-container">
                <label class="text-white">O'lchami: {{ imageWidth }}px</label>
                <input
                    type="range"
                    min="100"
                    max="500"
                    v-model.number="imageWidth"
                    @input="updateDimensions"
                />
              </div>
              <div class="slider-container">
                <label class="text-white">Uzunligi: {{ imageHeight }}px</label>
                <input
                    type="range"
                    min="100"
                    max="500"
                    v-model.number="imageHeight"
                    @input="updateDimensions"
                />
              </div>
              <div class="slider-container">
                <label class="text-white">Chap/o'ng: {{ imagePosition.x }}px</label>
                <input
                    type="range"
                    min="-200"
                    max="200"
                    v-model.number="imagePosition.x"
                    @input="updateDimensions"
                />
              </div>
              <div class="slider-container">
                <label class="text-white">Tepa/past: {{ imagePosition.y }}px</label>
                <input
                    type="range"
                    min="-200"
                    max="200"
                    v-model.number="imagePosition.y"
