                      </defs>
                      <g clip-path="url(#clip7_13311)">
                        <path d="M15 5L17 5C18.1 5 19 5.89 19 7L19 19C19 20.1 18.1 21 17 21L7 21C5.89 21 5 20.1 5 19L5 7C5 5.89 5.89 5 7 5L9 5M12 12L15 12M12 16L15 16M13 3C14.1 3 15 3.89 15 5C15 6.1 14.1 7 13 7L11 7C9.89 7 9 6.1 9 5C9 3.89 9.89 3 11 3L13 3ZM9 12L9.01 12M9 16L9.01 16" stroke="#323133" stroke-opacity="1.000000" stroke-width="2.000000" stroke-linejoin="round" stroke-linecap="round"/>
                      </g>
                    </svg>

