                <div>
                  <router-link to="/my-answers">
                    <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                      <path d="M9.00061 12L11.0006 14L15.0006 10M7.83535 4.69705C8.55288 4.63979 9.23407 4.35763 9.78193 3.89075C11.0604 2.80123 12.9408 2.80123 14.2193 3.89075C14.7672 4.35763 15.4483 4.63979 16.1659 4.69705C17.8403 4.83067 19.1699 6.16031 19.3036 7.83474C19.3608 8.55227 19.643 9.23346 20.1099 9.78132C21.1994 11.0598 21.1994 12.9402 20.1099 14.2187C19.643 14.7665 19.3608 15.4477 19.3036 16.1653C19.1699 17.8397 17.8403 19.1693 16.1659 19.303C15.4483 19.3602 14.7672 19.6424 14.2193 20.1093C12.9408 21.1988 11.0604 21.1988 9.78193 20.1093C9.23407 19.6424 8.55288 19.3602 7.83535 19.303C6.16092 19.1693 4.83128 17.8397 4.69766 16.1653C4.6404 15.4477 4.35824 14.7665 3.89136 14.2187C2.80184 12.9402 2.80184 11.0598 3.89136 9.78132C4.35824 9.23346 4.6404 8.55227 4.69766 7.83474C4.83128 6.16031 6.16092 4.83067 7.83535 4.69705Z" stroke="#323133" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                    </svg>

                    {{$t('test_results')}}
                  </router-link>
                </div>
              </div>
              <div class="drop_links prof_links">
                <div>
                  <router-link to="/">
                    <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                      <path d="M15 10L19.5528 7.72361C20.2177 7.39116 21 7.87465 21 8.61803V15.382C21 16.1253 20.2177 16.6088 19.5528 16.2764L15 14M5 18H13C14.1046 18 15 17.1046 15 16V8C15 6.89543 14.1046 6 13 6H5C3.89543 6 3 6.89543 3 8V16C3 17.1046 3.89543 18 5 18Z" stroke="#323133" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                    </svg>

                    {{$t('courses')}}
                  </router-link>
                </div>
              </div>
              <div class="drop_links prof_links">
                <div>
                  <router-link to="/my_resume">
                    <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                      <path d="M10 6H5C3.89543 6 3 6.89543 3 8V17C3 18.1046 3.89543 19 5 19H19C20.1046 19 21 18.1046 21 17V8C21 6.89543 20.1046 6 19 6H14M10 6V5C10 3.89543 10.8954 3 12 3C13.1046 3 14 3.89543 14 5V6M10 6C10 7.10457 10.8954 8 12 8C13.1046 8 14 7.10457 14 6M9 14C10.1046 14 11 13.1046 11 12C11 10.8954 10.1046 10 9 10C7.89543 10 7 10.8954 7 12C7 13.1046 7.89543 14 9 14ZM9 14C10.3062 14 11.4174 14.8348 11.8292 16M9 14C7.69378 14 6.58249 14.8348 6.17065 16M15 11H18M15 15H17" stroke="#323133" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
                    </svg>

                    {{$t('my_resume')}}
                  </router-link>
                </div>
              </div>
            </div>
          </div>
          <div class="col-xl-9">
            <div class="info_cards">
              <div class="info_title">
                {{$t('my_portfolio')}}
              </div>
              <div class="onliy_tit">
                Mos kasblar
              </div>
              <div class="row w-100">
                <div class="col-xl-6 card_mains_job" v-for="(item,indexs) in jobs.results" :key="indexs">
                  <div class="profision_card">
                      <div>
                        <img :src="item.profession.banner_image_url" alt="">
                      </div>
                      <div>
                        <div class="job-title">
                          {{item.profession.title}}
                        </div>
                        <div class="job_des" v-html="item.profession.workplace">

                        </div>
                        <div class="job_price">
                          <span>  Oylik maoshi:</span>
                          {{item.profession.min_salary}} som-{{item.profession.max_salary}} som
                        </div>
                        <div class="jobs_link">
                          <router-link :to="`/jobs/${item.id}`">
                            Batafsil tanishish
                            <div>
                              <svg width="24" height="24" viewBox="0 0 24 24" fill="none"
                                   xmlns="http://www.w3.org/2000/svg">
                                <path d="M10 7L15 12L10 17" stroke="#8F51E6" stroke-width="2"
                                      stroke-linecap="round" stroke-linejoin="round"/>
                              </svg>

                            </div>
                          </router-link>
                        </div>
                    </div>
