              <!--                  </router-link>-->
              <!--                </div>-->
              <!--              </div>-->
            </div>
          </div>
          <div class="col-xl-9">
            <div class="info_cards">
              <div class="info_title">
                {{$t('my_profile')}}
              </div>

              <div class="row w-100" >
                <div class="col-xl-6" >
                  <div class="tech_class_card" @click="$router.push('/teacher/classes')">
                    <div>
                      <div class="cl_name">
                       {{sc.school}}
                      </div>
                      <div class="cl_count">
                        {{sc.region_name}}
                      </div>
                    </div>
                    <div>
                      <svg width="24" height="24" viewBox="0 0 24 24" fill="none" xmlns="http://www.w3.org/2000/svg">
                        <path d="M10 7L15 12L10 17" stroke="#B3B2B1" stroke-width="2" stroke-linecap="round" stroke-linejoin="round"/>
